import React from 'react'

//--- Components
import Left from './Left/Left'
import Player from './Player/Player'
import Right from './Right/Right'


function Footer() {
    return (
        <div
            className="container-fluid fixed-bottom bg-secondary m-0 p-0"
            style={{height: "13%"}}
        >
            Footer
            <div className="row">
                <Left />
                <Player />
                <Right />
            </div>
        </div>
    )
}

export default Footer
